import React from "react";
import { Cell, Div, Link } from "@vkontakte/vkui";

const MainingItem = ({
  id,
  name,
  color,
  price,
  icon,
  scheme,
  speed,
  count,
}) => {
  return (
    <Div
      className={
        scheme === "bright_light"
          ? "cardBlack"
          : "cardWhite"
      }
    >
      <Cell
        className="myItem"
        before={icon}
        asideContent={
          <Div
            style={{
              color: "black",
              display: "flex",
              alignItems: "center",
              flexDirection: "row",
            }}
          >
            {speed}
            <Div className="iconDCMaining" />
            <Div style={{ margin: 0, padding: 0 }}>/сек</Div>
          </Div>
        }
        description={
          <Link id={id} onClick={null} mode="tertiary">
            купить
          </Link>
        }
      >
        {name}
      </Cell>
    </Div>
  );
};

export { MainingItem };
