import React from 'react';
import { useState, useEffect } from 'react';
import {
    Panel, PanelHeader, PanelHeaderBack,
    FixedLayout, Tabs, TabsItem, Div, Spinner,
    CellButton, PullToRefresh
} from '@vkontakte/vkui';
import Icon16Fire from '@vkontakte/icons/dist/16/fire';
import bridge from '@vkontakte/vk-bridge';
import axios from 'axios';



import { ShopItem, InventoryItem, MainingItem } from '../components';
import requestMethod from '../helpers/requestMethod';
import maining from '../helpers/dataMaining';


const Inventory = ({ id, go, dataInventory, openInfo, transfer, buyItem, $useItem,
    targetIdInventory, openUseItem, scheme, goHome, connectTwoDevices, stub, socketReconnect, networkStatus,
    openBuyItem, updateConnection, socketReconnection, onRefresh, fetching, snackbar,inventory }) => {
    // const [userToken, setUserToken] = useState(null);
    const [activeTab, setActiveTab] = useState('inventory');


    // useEffect(() => {
    //     getUsers();
    //     getGroups();
    // }, []);


    function tabInventory() {
        setActiveTab('inventory');
        window.scrollTo(0, 0);
    }

    function tabMaining() {
        setActiveTab('maining');
        window.scrollTo(0, 0);
    }

    function tabPromo() {
        setActiveTab('promo');
        window.scrollTo(0, 0);
    }



    const renderInventory = dataInventory.map((item, index) => {
        return (
            <InventoryItem
                key={index}
                id={item.id}
                name={item.name}
                price={item.price}
                icon={item.icon}
                count={item.count}
                openInfo={openInfo}
                transfer={transfer}
                buyItem={buyItem}
                openBuyItem={openBuyItem}
                $useItem={$useItem}
                targetIdInventory={targetIdInventory}
                openUseItem={openUseItem}
                scheme={scheme}
            />
        );
    });


    const renderMaining = maining.map((item, index) => {
        return (
            <MainingItem
                key={index}
                id={item.id}
                name={item.name}
                price={item.price}
                icon={item.icon}
                speed={item.speed}
                scheme={scheme}
            />
        );
    });

    return (
        <Panel id={id}>
            <PanelHeader left={<PanelHeaderBack onClick={goHome} data-to="home" />}>
                Инвентарь
            </PanelHeader>


            {connectTwoDevices
                ?
                stub('Вы подключились с 2 устройств ', socketReconnect, "Переподключиться")
                :
                <>
                    <FixedLayout vertical="top" filled={true}>
                        <Tabs >
                            <TabsItem
                                onClick={tabInventory}
                                selected={activeTab === 'inventory'}
                            >
                                Имущество
                           </TabsItem>
                            {/* <TabsItem
                                onClick={tabMaining}
                                selected={activeTab === 'maining'}
                            >
                                Майнеры
                            </TabsItem> */}
                        </Tabs>
                    </FixedLayout>


                    {networkStatus
                        ?
                        stub('Отсутствует интернет - соединение!', updateConnection, "Повторить")
                        :
                        <>
                            {socketReconnection
                                ?
                                stub('Ошибка подключения к серверу', socketReconnect, "Повторить")
                                :
                                <>
                                    {activeTab === 'inventory'
                                        ?
                                        <>
                                            {renderInventory.length
                                                ?
                                                <Div style={{ marginTop: 55}}>
                                                    {renderInventory}
                                                </Div>
                                                :
                                                <div style={{ display: 'flex', alignItems: 'center', flexDirection: 'column' }}>
                                                    <Spinner size="large" style={{ marginTop: 90 }} />
                                                </div>
                                            }
                                        </>
                                        :
                                        ''
                                    }


                                    {activeTab === 'maining'
                                        ?
                                        <>
                                            {renderMaining.length
                                                ?
                                                <Div style={{ marginTop: 55}}>
                                                    {renderMaining}
                                                </Div>
                                                :
                                                <div style={{ display: 'flex', alignItems: 'center', flexDirection: 'column' }}>
                                                    <Spinner size="large" style={{ marginTop: 90 }} />
                                                </div>
                                            }
                                        </>
                                        :
                                        ''
                                    }

                                </>
                            }

                            {snackbar}
                        </>
                    }
                </>
            }
        </Panel >
    );
};

export { Inventory };
