import React from "react";
import { useState, useEffect } from "react";
import {
  Panel,
  PanelHeader,
  PanelHeaderBack,
  FixedLayout,
  Tabs,
  TabsItem,
  Div,
  Spinner,
  CellButton,
  PullToRefresh,
} from "@vkontakte/vkui";
import Icon16Fire from "@vkontakte/icons/dist/16/fire";
import bridge from "@vkontakte/vk-bridge";
import axios from "axios";

import styles from "../styles/custom-styles.sass";
import { ShopItem, InventoryItem, MainingItem } from "../components";
import requestMethod from "../helpers/requestMethod";
import { formatCoins } from "../helpers";
import shop from "../helpers/dataShop";
import maining from "../helpers/dataMaining";

const Shop = ({
  id,
  go,
  dataInventory,
  openInfo,
  transfer,
  buyItem,
  $useItem,
  targetIdInventory,
  openUseItem,
  scheme,
  goHome,
  connectTwoDevices,
  stub,
  socketReconnect,
  networkStatus,
  openBuyItem,
  updateConnection,
  socketReconnection,
  onRefresh,
  fetching,
  snackbar,
  durkaCoins,
}) => {
  // const [userToken, setUserToken] = useState(null);
  const [activeTab, setActiveTab] = useState("product");

  // useEffect(() => {
  //     getUsers();
  //     getGroups();
  // }, []);

  function tabProduct() {
    setActiveTab("product");
    window.scrollTo(0, 0);
  }

  function tabMaining() {
    setActiveTab("maining");
    window.scrollTo(0, 0);
  }

  const renderShop = shop.map((item, index) => {
    return (
      <ShopItem
        key={index}
        id={item.id}
        name={item.name}
        price={item.price}
        icon={item.icon}
        openInfo={openInfo}
        transfer={transfer}
        buyItem={buyItem}
        openBuyItem={openBuyItem}
        scheme={scheme}
      />
    );
  });

  const renderMaining = maining.map((item, index) => {
    return (
      <MainingItem
        key={index}
        id={item.id}
        name={item.name}
        price={item.price}
        icon={item.icon}
        speed={item.speed}
        scheme={scheme}
      />
    );
  });

  return (
    <Panel id={id}>
      <PanelHeader left={<PanelHeaderBack onClick={goHome} data-to="home" />}>
        Магазин
      </PanelHeader>

      {connectTwoDevices ? (
        stub(
          "Вы подключились с 2 устройств ",
          socketReconnect,
          "Переподключиться"
        )
      ) : (
        <>
          <FixedLayout vertical="top" filled={true}>
            <Tabs>
              <TabsItem onClick={tabProduct} selected={activeTab === "product"}>
                Товары
              </TabsItem>
              {/* <TabsItem onClick={tabMaining} selected={activeTab === "maining"}>
                Майнеры
              </TabsItem> */}
            </Tabs>
          </FixedLayout>

          {networkStatus ? (
            stub(
              "Отсутствует интернет - соединение!",
              updateConnection,
              "Повторить"
            )
          ) : (
            <>
              {socketReconnection ? (
                stub(
                  "Ошибка подключения к серверу",
                  socketReconnect,
                  "Повторить"
                )
              ) : (
                <>
                  {activeTab === "product" ? (
                    <>
                      {renderShop.length ? (
                        <Div style={{ marginTop: 55 }}>
                          <Div
                            style={{
                              display: "flex",
                              alignItems: "center",
                              flexDirection: "row",
                              marginLeft: 25,
                              marginBottom: 10
                            }}
                          >
                            Баланс: {formatCoins(durkaCoins)}
                            <Div className="iconDC" style={{ marginLeft: 8 }} />
                          </Div>

                          {renderShop}
                        </Div>
                      ) : (
                        <div
                          style={{
                            display: "flex",
                            alignItems: "center",
                            flexDirection: "column",
                          }}
                        >
                          <Spinner size="large" style={{ marginTop: 90 }} />
                        </div>
                      )}
                    </>
                  ) : (
                    ""
                  )}

                  {activeTab === "maining" ? (
                    <>
                      {renderMaining.length ? (
                        <Div style={{ marginTop: 55 }}>{renderMaining}</Div>
                      ) : (
                        <div
                          style={{
                            display: "flex",
                            alignItems: "center",
                            flexDirection: "column",
                          }}
                        >
                          <Spinner size="large" style={{ marginTop: 90 }} />
                        </div>
                      )}
                    </>
                  ) : (
                    ""
                  )}
                </>
              )}
              {snackbar}
            </>
          )}
        </>
      )}
    </Panel>
  );
};

export { Shop };
